import {useState} from "react";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import FileUploadIcon from '@mui/icons-material/FileUpload';
import Button from "@mui/material/Button";


function FileUploadInput() {
    const [fileSelected, setFileSelected] = useState(false)

    const handleClick = () => {
        setFileSelected((prev) => !prev)
    }

    return (
        <>
            <div>


                <Box sx={{
                    // outline: '2px solid red',
                    p: 2,
                    minHeight: 90,
                    display: 'flex',
                    alignItems: 'center',
                    backgroundColor: 'grey.200',
                    // color: fileSelected ? 'grey.900' : 'grey.600',
                    border: '2px dashed',
                    borderColor: 'grey.400',
                    width: '100%',
                    textAlign: 'center',
                    justifyContent: 'center',
                }}>
                    <div>
                        <Typography variant="subtitle1" color="text.secondary">
                            A szerződéshez szükséfes fájl feltöltése
                        </Typography>
                        {!fileSelected && <FileUploadIcon fontSize="large" sx={{color: 'grey.600','mt':1}}/>}
                        <Typography variant={fileSelected ? "subtitle2" : "subtitle1"}
                                    sx={{mt: 1, color: fileSelected ? 'grey.900' : 'grey.600',}}>
                            {fileSelected ? "Nagy Laci-szerzodeshez! v04 final++(2).docx" : "Húzd ide a fájlt"}
                        </Typography>
                    </div>
                </Box>
                <Box sx={{'display': 'flex','justifyContent': 'center'}}>
                    <Button size="small" variant={fileSelected ? "contained" : "contained"} sx={{'mt':1}}
                            onClick={handleClick}>{fileSelected ? "Másik fájl választása" : "Fájl kiválasztása"}</Button>
                </Box>
            </div>
        </>
    )
}

export default FileUploadInput;